<template>
  <div class="myarticle-container page-container">
    <!-- 导航栏 -->
    <van-nav-bar
      class="page-nav-bar"
      left-arrow
      fixed
      @click-left="$router.back()"
    ></van-nav-bar>
    <!-- /导航栏 -->

    <!-- 文章标签列表 -->
    <van-tabs
      class="fixed-tabs"
      v-model="active"
      title-active-color="#3c9bfa"
      color="#399afa"
    >
      <van-tab :title="tab.title" v-for="tab in tabs" :key="tab.title">
        <loading-list :fn="tab.fn" v-slot="{ item }">
          <article-item :article="item" />
        </loading-list>
      </van-tab>
    </van-tabs>
    <!-- /文章标签列表 -->
  </div>
</template>

<script>
import LoadingList from '@/components/loading-list'
import ArticleItem from './components/article-item'
import { getUserCollectArticles, getUserHistoryArticles } from '@/api/article'

export default {
  name: 'MyArticle',
  components: {
    LoadingList,
    ArticleItem
  },
  props: {
    // 路由传过来的参数
    type: {
      type: String
    }
  },
  data() {
    return {
      active: this.getInitialActive(),
      tabs: [
        {
          title: '收藏',
          fn: getUserCollectArticles
        },
        {
          title: '历史',
          fn: getUserHistoryArticles
        }
      ]
    }
  },
  computed: {},
  watch: {},
  created() {},
  mounted() {
    this.$store.commit('addCachePage', 'MyArticle')
  },
  methods: {
    getInitialActive() {
      const active = ['collect', 'history', undefined].indexOf(this.type)
      return active !== -1 ? active : 0
    }
  },
  beforeRouteLeave(to, from, next) {
    // 导航离开该组件的对应路由时调用
    // 可以访问组件实例 `this`
    // 如果不是去 文章详情 页面，则清除缓存
    const { name: pageName } = this.$options
    if (to.name !== 'article') {
      this.$store.commit('removeCachePage', pageName)
    }
    next()
  }
}
</script>

<style scoped lang="less">
.fixed-tabs {
  top: 92px;
}
/deep/.van-tabs__content {
  position: fixed;
  top: 200px;
  bottom: 0;
  left: 0;
  right: 0;
  overflow-y: auto;
}
</style>
